.logArea{
    padding: 200px 5% 150px;
    text-align: center;
    background-color: #04043d;
    background-image: linear-gradient(rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.8));
    color: #fff;
}
.logArea h2{
    font-size: 50px;
    font-weight: 300;
}
.logArea p{
    font-size: 30px;
    font-weight: 100px;
}
.logArea button{
    padding: 10px 20px;
    background-color: transparent;
    border: 2px solid #00ff00;
    font-size: 18px;
    color: #fff;
}

.logArea button:hover{
    background-color: #00ff00;
    font-weight: 600;
}
