.detail_box{
    padding: 50px 5% ;
    width: 92%;
    margin: auto;
    position: fixed;
    z-index: 5;
    overflow-y: scroll;
    left: 5%;
    top: 0%;
    background-color: rgba(0, 0, 0, 0.9);
    height: 100%;
}
.bottomSpace{
    padding: 50px;
}

.detail_box h2{
    color: #ccc;
}



.detail_box ul li, .detail_box p, .detail_box i{
    color: #fff;
}

.detail_box h4{
    margin-bottom: 15px;
    color: #ff4500;
}
.detail_box h3{
    margin-bottom: -10px;
    display: flex;
    justify-content: space-between;
    color: #ff4500;
}
/* .detail_box section{
    border-bottom: 1px solid #ccc;
} */

.apply{
    color: #fff;
    background-color: #0f5f0f;
    border: 0px;
    border-radius: 5px;
    font-size: 18px;
    padding: 8px 15px;
    margin-top: 40px;
    width: 100%;
}
.apply:hover{
    cursor: pointer;
    background-color: #ff4500;
}

.hideApply{
    position: absolute;
    top: 10px;
    right: 10%;
    color: #ff4500;
    z-index: 12;
    border: 0px;
    font-size: 40px;
    background-color: transparent;
}
.hideApply:hover{
    color: #fff;
    cursor: pointer;
}

@media only screen and (min-width: 800px) {
    .job_description{
        display: flex;
    }
    .job_description > div{
        width: 50%;
        padding: 0px 20px 0px;
    }
    .apply{
        width: 300px;
        margin: auto;
        display: block;
    }
}

