*{
    box-sizing: border-box;
}
.apply{
    width: 90%;
    background-color: #fff;
    position: absolute;
    left: 5%;
    top: 0%;
    padding: 80px 40px 30px 30px;
    display: block;
    z-index: 10;
    height: 130%;
    margin: auto;

}

.file{
    font-size: 18px;
    padding: 8px 5px;
    margin: 10px 0px 20px;
}

.apply form input, .apply form textarea, .apply form select{
    
    margin: 10px 0px 20px;
    padding: 8px 5px;
    position: relative;
}

.apply form input, .apply form textarea{
    width: 100%;
}
.apply form select{
    width: 102.5%;
}


@media only screen and (min-width: 800px) {
    
    .apply form div {
        width: 50%;
        display: inline-block;
        padding: 20px;
    }
}