.search_section{
    padding: 20px;
    overflow: hidden;
    background-color: #fff;
    margin: 0px 5% 100px;
    background-color: #fff;
    box-shadow: 0px 0px 2px 2px #ff4500;
}
.search_section select{
    width: 100%;
    font-size: 18px;
    padding: 5px;
    margin: 15px 0px ;
}

.search_btn{
    float: right;
    padding: 8px 20px;
    font-size: 18px;
}

.search_section legend{
    font-size: 30px;
    margin-bottom: 20px;
    font-weight: 600;
    text-align: center;
}
.search_section label{
    font-size: 20px;
    font-weight: 300;
}

@media only screen and (min-width: 900px) {
    .search_btn{
        float: none;
        width: 100%;
    }
}