@import url('https://fonts.googleapis.com/css?family=Roboto:100,300,400,500,700,900');

body {
    font-family: 'Roboto', sans-serif;
}
.row {
    margin-right: -15px;
    margin-left: -15px;
  }

  .container {
    padding-right: 15px;
    padding-left: 15px;
    margin-right: auto;
    margin-left: auto;
  }
  ul,
ol {
  margin-top: 0;
  margin-bottom: 10px;
}
ul ul,
ol ul,
ul ol,
ol ol {
  margin-bottom: 0;
}

  @media (min-width: 768px) {
    .container {
      width: 750px;
    }
  }
  @media (min-width: 992px) {
    .container {
      width: 970px;
    }
  }
  @media (min-width: 1200px) {
    .container {
      width: 1170px;
    }
  }

  .col_xs_1,
.col_sm_1,
.col_md_1,
.col_lg_1,
.col_xs_2,
.col_sm_2,
.col_md_2,
.col_lg_2,
.col_xs_3,
.col_sm_3,
.col_md_3,
.col_lg_3,
.col_xs_4,
.col_sm_4,
.col_md_4,
.col_lg_4,
.col_xs_5,
.col_sm_5,
.col_md_5,
.col_lg_5,
.col_xs_6,
.col_sm_6,
.col_md_6,
.col_lg_6,
.col_xs_7,
.col_sm_7,
.col_md_7,
.col_lg_7,
.col_xs_8,
.col_sm_8,
.col_md_8,
.col_lg_8,
.col_xs_9,
.col_sm_9,
.col_md_9,
.col_lg_9,
.col_xs_10,
.col_sm_10,
.col_md_10,
.col_lg_10,
.col_xs_11,
.col_sm_11,
.col_md_11,
.col_lg_11,
.col_xs_12,
.col_sm_12,
.col_md_12,
.col_lg_12 {
  position: relative;
  min-height: 1px;
  padding-right: 15px;
  padding-left: 15px;
}
.col_xs_12 {
  width: 100%;
  float: left;
}


@media (min-width: 768px) {
    
    
    .col_sm_6 {
      width: 50%;
      float: left;
    }
    
}

@media (min-width: 992px) {
    
    .col_md_3 {
      width: 30%;
      float: left;
    }
    
}



  .text_center {
    text-align: center;
  }

.search_text {
	background-color: #030353;
	padding-top: 60px;
	padding-bottom: 60px;
}
	
.search_text .input_search {
	height: 45px;
	width: 40%;
	padding-left: 20px;
    color: #333;
} 

.search_text .btn_search {
    background: #da3e44;
    border: none;
	color: #FFF;
	height: 45px;
    width: 80px;
}

.search_text h4 {
    color: #FFF;
    font-weight: 700;
}

footer {
    background-color: #04043d;
    padding: 30px 0px;
}	       

.logo {
    color: #FFF;
    font-weight: 700;
    font-size: 30px;
}

.address span, 
.menu span {
   color: #FFF; 
   font-weight: bold; 
   border-bottom: 1px solid #c7c7c7; 
   padding: 10px 0px;
   display: block;
   text-transform: uppercase;
   font-size: 16px;
   letter-spacing: 3px;
}
.row:before,
.row:after{
    display: table;
    content: " ";
  }
  .row::after{
    clear: both;
  }
article
footer,
header,
menu,
nav,
section
 {
  display: block;
}
 
.address li a, 
.menu li a {
    color: #FFF;
    letter-spacing: 3px;
    text-decoration: none;
    font-size: 14px;
}

.address li, 
.menu li {
    margin: 20px 0px;
    list-style: none;
}

.address li a:hover, 
.menu li a:hover {
    color: #da3e44;
    transition: all 1s ease-in-out;
    -webkit-transition: all 1s ease-in-out;
    -moz-transition: all 1s ease-in-out;
    -o-transition: all 1s ease-in-out;
}

.address .fa {
    color: #da3e44;
    margin-right: 10px;
    font-size: 18px;
}