.save_article{
    padding: 200px 0px 100px;
    overflow: hidden;
    background-color: #eee;
}

.no_save{
    padding: 5% 80px;
    font-size: 50px;
    color: #333;
    text-align: center;
    border: 2px dotted #04043d;
    background-color: #ffffff;
    margin: 10% 5% ;
}
.delBtn{
    padding: 5px 15px;
    border: 0px;
    font-size: 16px;
    border-radius: 5px;
    background-color: #6d0606;
    color: #fff;
}


.delBtn:hover{
    box-shadow: 0px 0px 2px 2px #ccc;
}