.feed{
    margin: 0px 5%;
    border-bottom: 2px solid #ccc;
    padding: 10px 20px 20px;
    background-color: #fff;
}
.feed h4{
    margin-bottom: -15px;
}
.act{
    padding: 5px 15px;
    border: 0px;
    font-size: 16px;
    border-radius: 5px;
    background-color: #4169e1;
    color: #fff;
}
.act:hover{
    box-shadow: 0px 0px 2px 2px #ccc;
}
.title{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.feed_details{
    margin-top: 30px;
}
.feed_details h4{
    font-weight: 500;
    color: #4169e1;
}
.feed_details p{
    margin-top: 30px;
}
.title{
    font-weight: 400;
    cursor: pointer;
}
.title:hover{
    color: #ff4500;
}
.feed_article{
    background-color: #eee;
    padding: 180px 0px 50px;
    position: relative;
}

.remove{
    z-index: 6;
    position: fixed;
    top: 20px;
    color: #fff;
    right: 10%;
    background-color: transparent;
    border: 0px;
    font-size: 50px;
    
}

.greetings{
    color: #04043d;
    text-align: center;
    font-size: 40px;
    font-weight: 400;
}

.greetings span{
    color: #ff4500;
}





.remove:hover{
    color: #ff4500;
    cursor: pointer;
}
.nextPrev{
    text-align: center;
    margin: 30px 0px 0px;
}



@media only screen and (min-width: 900px) {
    .feed_section{
        overflow: hidden;
        
    }
    .search_box{
        width: 35%;
        float: left;
        left: 5%;
    }
    .feed_block{
        width: 60%;
        float: right;
    }
    

}