.logo{
    color: #fff;
}
.nav{
    background-color: #04043d;
    height: auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px 5%;
    margin-top: 0px;
    position: fixed;
    z-index: 5;
    top: 0px;
    left: 0px;
    width: 100%;
}
.profileImg{
    width: 70px;
    height: 70px;
    border-radius: 50px;
    margin-left: 30px;
}

.nav ul{
    list-style-type: none;
    
}
.nav .desktopMenu ul{
    margin: 0px;
    height: inherit;
    width: auto;
    text-align: left;
}
.nav .desktopMenu li{
    display: inline-block;
    padding: 5px;
    margin: 0px 5px;
}

.nav a:link{
    color: #fff;
    text-decoration: none;
}
.nav a:active{
    color: #ff4500;
}
.nav a:visited{
    color: #fff;
}
.nav a:hover{
    color: #ff4500;
}
.menu_logo{
    display: flex;
}
.right_head{
    display: flex;
    align-items: center;
}

.nav .sideMenu{
    height: 100%;
    z-index: 4;
    top: -20px;
    left: 0px;
    width: 50%;
    position: fixed;
    background-color: rgba(0, 0, 0, 0.4);
    padding-top: 30%;
}
.nav .sideMenu li{
    display: block;
    padding: 5px;
    margin: 20px 0px;
    text-align: center;
}

.nav .sideMenu button{
    position: absolute;
    left: 80%;
    top: 10%;
    transform: translate(50%, 50%);
    background-color: transparent;
    color: #eb3c3c;
    font-size: 50px;
    border: 0px;
    font-weight: 100;
}
.nav .sideMenu button:hover{
    color: #fff;
    cursor: pointer;
}
.menu_logo button{
    background: transparent;
    border: 1px solid #ff4500;
    font-size: 18px;
    color: #ff4500;
    margin-right: 20px;
}
.menu_logo button:hover{
    background-color: #fff;
}

@media only screen and (max-width: 700px) {
    .desktopMenu{
        display: none;
    }
}